body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  align-items: center;
}

.result{
  width:100%;
  display: block;
}
.app{
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-content: center;
    align-items: center;
} 


.app div img{
    width: 20vw;
    height: 20vw;
    
}

.app button{
    margin: 5px;
    width: 220px;
    height: 40px;
    padding: 10px;
   font-size: 18px;
   border-radius: 5px;
   padding-bottom: 30px;
}

.app h1{
    font-size: 20px;
}
